import { BASE_URL } from "./constants";

export default class Api {
  constructor({ baseUrl, headers }) {
    this._baseUrl = baseUrl;
    this._headers = headers;
  }

  _checkResponse(res) {
    /**
     * –ø—Ä–æ–≤–µ—Ä–∫–∞ –æ—Ç–≤–µ—Ç–∞ —Å–µ—Ä–≤–µ—Ä–∞ –Ω–∞ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ—Å—Ç—å:
     * –Ω–µ—É—Å–ø–µ—à–Ω—ã–µ –∑–∞–ø—Ä–æ—Å—ã –∏ –æ—Ç–≤–µ—Ç—ã –≤ fetch –≤—Å–µ —Ä–∞–≤–Ω–æ —Ä–µ–∑–æ–ª–≤—è—Ç –ø—Ä–æ–º–∏—Å,
     * –ø–æ—ç—Ç–æ–º—É –≤–∞–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä—è—Ç—å –±—É–ª–µ–≤—Å–∫–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ res.ok */

    /**
     * @params res - –ø—Ä–æ–º–∏—Å –ø–æ–ª—É—á–µ–Ω–Ω—ã–π –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞ —Å –ø–æ–º–æ—â—å—é fetch
     * @returns –≤ —Å–ª—É—á–∞–µ —É—Å–ø–µ—à–Ω–æ–≥–æ –æ—Ç–≤–µ—Ç–∞ - json —Å –¥–∞–Ω–Ω—ã–º–∏, –∏–Ω–∞—á–µ - —Ä–µ–¥–∂–µ–∫—Ç –ø—Ä–æ–º–∏—Å–∞
     */
    if (res.ok) return res.json();
    return Promise.reject(`–û—à–∏–±–∫–∞ –∑–∞–ø—Ä–æ—Å–∞ –Ω–∞ —Å–µ—Ä–≤–µ—Ä: ${res.status}`);
  }

  getIngredients() {
    /**
     * GET –∑–∞–ø—Ä–æ—Å –¥–∞–Ω–Ω—ã—Ö —Å–æ —Å–ø–∏—Å–∫–æ–º –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–æ–≤ —Å —Å–µ—Ä–≤–µ—Ä–∞ */

    /**
     * @returns –ø—Ä–æ–º–∏—Å –ø–æ–ª—É—á–µ–Ω–Ω—ã–π –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞ —Å –ø–æ–º–æ—â—å—é fetch
     */
    return fetch(`${this._baseUrl}/ingredients`, {
      headers: this._headers,
    }).then(this._checkResponse);
  }
}

export const api = new Api({
  baseUrl: BASE_URL,
  headers: {
    "Content-Type": "application/json",
  },
});
