import { useMemo } from "react";
import PropTypes from "prop-types";
import constructorStyles from "./burger-constructor.module.css";
import appStyles from "../app/app.module.css";
import {
  ConstructorElement,
  DragIcon,
  CurrencyIcon,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { dataPropTypes } from "../../utils/types";

function BurgerConstructor({
  data,
  onOpenModalWithIngredient,
  onOpenModalWithOrder,
}) {
  const onClickToOrderDetails = (number) => {
    onOpenModalWithOrder({ orderNumber: number });
  };

  const onClickToIngredient = (id) => {
    onOpenModalWithIngredient({ itemId: id });
  };

  const generateKey = (id) => {
    return `${id}_${new Date().getTime()}`;
  };

  const bun = data.find((item) => item.type === "bun");
  const nonBunElements = useMemo(() => {
    /**
     * –¢.–∫. –≤ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–µ –º–æ–≥—É—Ç –±—ã—Ç—å –ø–æ–≤—Ç–æ—Ä—è—é—â–∏–µ—Å—è —ç–ª–µ–º–µ–Ω—Ç—ã —Å –æ–¥–∏–Ω–∞–∫–æ–≤—ã–º–∏ _id, –∞ –∑–Ω–∞—á–∏—Ç –¥–ª—è React key –æ–Ω –Ω–µ –ø–æ–¥—Ö–æ–¥–∏—Ç.
     * –ù–∞ —Ç–µ–∫—É—â–µ–º —ç—Ç–∞–ø–µ —Å–≥–µ–Ω–µ—Ä–∏—Ä—É–µ–º —É–Ω–∏–∫–∞–ª—å–Ω—ã–π uid –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—É –≤ –º–æ–º–µ–Ω—Ç —Å–æ–∑–¥–∞–Ω–∏—è –º–∞—Å—Å–∏–≤–∞ nonBunElements.
     * –î–µ–ª–∞—Ç—å —ç—Ç–æ –Ω—É–∂–Ω–æ —Ç–æ–ª—å–∫–æ –≤ —Ç–æ—Ç –º–æ–º–µ–Ω—Ç –∫–æ–≥–¥–∞ –º–µ–Ω—è–µ—Ç—Å—è –º–∞—Å—Å–∏–≤ –∏—Å—Ö–æ–¥–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö, –ø–æ—ç—Ç–æ–º—É –≤—ã—á–∏—Å–ª–µ–Ω–∏–µ –º–µ–º–æ–∏–∑–∏—Ä—É–µ–º
     * —á–µ—Ä–µ–∑ useMemo —Å –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç—è–º–∏ [data]. –¢–µ–ø–µ—Ä—å uid –Ω–µ –±—É–¥–µ—Ç –º–µ–Ω—è—Ç—å—Å—è –ø—Ä–∏ –∫–∞–∂–¥–æ–π –ø–µ—Ä–µ—Ä–∏—Å–æ–≤–∫–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞,
     * –∏ –º—ã –º–æ–∂–µ–º –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –µ–≥–æ –≤ –∫–∞—á–µ—Å—Ç–≤–µ key.
     */
    return data
      .filter((item) => item.type !== "bun")
      .map((item) => ({ ...item, uid: generateKey(item._id) }));
  }, [data]);

  return (
    <section
      className={`${constructorStyles.constructor__container} pt-25 pb-2 pl-4`}
    >
      <div
        className={`${constructorStyles.constructor__bunTop} mr-4`}
        onClick={() => onClickToIngredient(bun._id)}
      >
        <ConstructorElement
          type={bun.type}
          isLocked={true}
          text={`${bun.name} (–≤–µ—Ä—Ö)`}
          price={bun.price}
          thumbnail={bun.image}
        />
      </div>
      <ul
        className={`${constructorStyles.constructor__nonBunElements} ${appStyles.scroll} pt-4`}
      >
        {nonBunElements.map((item) => (
          <li
            className={`${constructorStyles.constructor__nonBunElement} mb-4 ml-2`}
            key={item.uid}
            onClick={() => onClickToIngredient(item._id)}
          >
            <DragIcon type={"primary"} />
            <ConstructorElement
              text={item.name}
              price={item.price}
              thumbnail={item.image}
            />
          </li>
        ))}
      </ul>
      <div
        className={`${constructorStyles.constructor__bunBottom} mr-4`}
        onClick={() => onClickToIngredient(bun._id)}
      >
        <ConstructorElement
          type={bun.type}
          isLocked={true}
          text={`${bun.name} (–Ω–∏–∑)`}
          price={bun.price}
          thumbnail={bun.image}
        />
      </div>
      <div
        className={`${constructorStyles.constructor__totalContainer} mt-10 mr-4`}
      >
        <div className={`${constructorStyles.constructor__totalPrice} mr-10`}>
          <p className="text text_type_digits-medium mr-2">610</p>
          <CurrencyIcon />
        </div>
        <Button
          type="primary"
          size="medium"
          onClick={() => onClickToOrderDetails("034536")} // –≤—Ä–µ–º–µ–Ω–Ω–æ —Ö–∞—Ä–¥–∫–æ–¥ –Ω–æ–º–µ—Ä–∞ –∑–∞–∫–∞–∑–∞ –ø–æ –º–∞–∫–µ—Ç—É
        >
          –û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑
        </Button>
      </div>
    </section>
  );
}

BurgerConstructor.propTypes = {
  onOpenModalWithIngredient: PropTypes.func.isRequired,
  onOpenModalWithOrder: PropTypes.func.isRequired,
  data: PropTypes.arrayOf(dataPropTypes).isRequired,
};

export default BurgerConstructor;
