import { useState, useEffect } from "react";
import appStyles from "./app.module.css";
import AppHeader from "../app-header/app-header";
import BurgerIngredients from "../burger-ingredients/burger-ingredients";
import BurgerConstructor from "../burger-constructor/burger-constructor";
import Modal from "../modal/modal";
import OrderDetails from "../order-details/order-details";
import IngredientDetails from "../ingredient-details/ingredient-details";
import { api } from "../../utils/api";

function App() {
  const [ingredientToView, setIngredientToView] = useState(null);
  const [orderNumber, setOrderNumber] = useState(null);

  const [state, setState] = useState({
    isLoading: false,
    hasError: false,
    data: [],
  });

  useEffect(() => {
    const getIngredientsData = async () => {
      setState({ ...state, hasError: false, isLoading: true });
      try {
        const data = await api.getIngredients();
        /* –≤ —Ç–∞–∫–æ–π —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏ state –ø–æ—Å–ª–µ await –º–æ–∂–µ—Ç –±—ã—Ç—å —É–∂–µ –Ω–µ –∞–∫—Ç—É–∞–ª—å–Ω—ã–π,
        –Ω—É–∂–Ω–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å setState —Å —Ñ—É–Ω–∫—Ü–∏–µ–π, —á—Ç–æ–±—ã –ø—Ä–∏–º–µ–Ω—è—Ç—å –∞–∫—Ç—É–∞–ª—å–Ω—ã–π —Å—Ç–µ–π—Ç */
        setState((prevState) => ({
          ...prevState,
          data: data.data,
          isLoading: false,
        }));
      } catch (err) {
        setState((prevState) => ({
          ...prevState,
          hasError: true,
          isLoading: false,
        }));
      }
    };
    getIngredientsData();
  }, []);

  const { data, isLoading, hasError } = state;

  const handleIngredientModalOpen = ({ itemId }) => {
    setIngredientToView(data.find((item) => item._id === itemId));
  };

  const handleOrderModalOpen = ({ orderNumber }) => {
    setOrderNumber(orderNumber);
  };

  const handleModalClose = () => {
    setIngredientToView(null);
    setOrderNumber(null);
  };

  return (
    <>
      <AppHeader />
      <main className={appStyles.page}>
        {isLoading && (
          <p className="text text_type_main-medium text_color_inactive pt-10">
            –ó–∞–≥—Ä—É–∑–∫–∞...
          </p>
        )}
        {hasError && (
          <p className="text text_type_main-medium text_color_inactive pt-10">
            –û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –¥–∞–Ω–Ω—ã—Ö
          </p>
        )}
        {!isLoading && !hasError && data.length && (
          <>
            <BurgerIngredients
              data={data}
              onOpenModal={handleIngredientModalOpen}
            />
            <BurgerConstructor
              data={data}
              onOpenModalWithOrder={handleOrderModalOpen}
              onOpenModalWithIngredient={handleIngredientModalOpen}
            />
            {ingredientToView && (
              <Modal title="–î–µ—Ç–∞–ª–∏ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–∞" onClose={handleModalClose}>
                <IngredientDetails
                  image={ingredientToView.image}
                  name={ingredientToView.name}
                  fat={ingredientToView.fat}
                  carbohydrates={ingredientToView.carbohydrates}
                  calories={ingredientToView.calories}
                  proteins={ingredientToView.proteins}
                />
              </Modal>
            )}
            {orderNumber && (
              <Modal onClose={handleModalClose}>
                <OrderDetails orderNumber={orderNumber} />
              </Modal>
            )}
          </>
        )}
      </main>
    </>
  );
}

export default App;
